<template>
<div class="edit-border panel">
  <edit-len v-model="border.width" label="" :with-unit="false"></edit-len>
  <form-field label="" v-model="border.style" type="select" :options="lineStyles"></form-field>
  <form-field label="">
    <color-pickr v-model="border.color"></color-pickr>
  </form-field>
  <edit-len v-model="border.radius" label="" :with-unit="false"></edit-len>
</div>
</template>

<script>
import ItemBlock from './ItemBlock'
import FormField from '../../common/components/FormField'
import EditLen from './EditLen'
import ColorPickr from '../../common/components/ColorPickr'
const lineStyles = {
  solid: '',
  dotted: '',
  dashed: '',
  double: '',
  none: '',
  inset: ''
}
const allSides = {
  top: '',
  bottom: '',
  left: '',
  right: ''
}
export default {
  name: 'EditBorder',
  components: {
    ColorPickr,
    EditLen,
    FormField,
    ItemBlock
  },
  props: {
    value: {
      type: Object
    }
  },
  data () {
    return {
    }
  },
  computed: {
    border () {
      return this.value
    },
    allSides () {
      return allSides
    },
    lineStyles () {
      return [{
        key: 'solid',
        value: ''
      }, {
        key: 'dotted',
        value: ''
      }, {
        key: 'dashed',
        value: ''
      }, {
        key: 'double',
        value: ''
      }, {
        key: 'none',
        value: ''
      }, {
        key: 'inset',
        value: ''
      },]
    }
  },
  watch: {
    'border': {
      handler (val) {
        this.$emit('input', val)
      },
      deep: true
    }
  },
  methods: {
    setRadiusSame () {
      const val = this.value.radius[0]
      this.value.radius = [val, val, val, val]
      this.$emit('input', this.value)
    }
  }
}
</script>

<style lang="scss">
.edit-border {
}
</style>
